.address__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 320px;
  margin: 0 auto;
}

@media (min-width: $tablet-version) {
  .address__wrapper {
    width: 768px;
  }
}

@media (min-width: $desktop-version) {
  .address {
    position: relative;

    width: 100%;
    margin: 0 auto;
  }

  .address__wrapper {
    position: absolute;
    top: 95px;
    left: 110px;
    width: 570px;
    z-index: 1;

    background-color: $white;
  }
}

.address__wrapper--bg-grey {
  background-color: #f2f2f2;
}


.address__text {
  margin: 0;
  padding-left: 19px;
  padding-top: 31px;
  padding-bottom: 26px;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

@media (min-width: $tablet-version) {
  .address__text {
    width: 154px;
    padding-left: 70px;
    padding-top: 66px;
    padding-bottom: 60px;

    font-size: 20px;
    line-height: 25px;
  }
}

@media (min-width: $desktop-version) {
  .address__text {
    padding-left: 75px;


  }
}

.address__map {
  position: relative;

  width: 320px;
  height: 400px;
  margin: 0 auto;
}

@media (min-width: $tablet-version) {
  .address__map {
    width: 768px;

  }
}

@media (min-width: $desktop-version) {
  .address__map {
    width: 100%;
  }
}

@media (min-width: $tablet-version) {
  .address__img-map {
    width: 768px;
    height: 400px;
  }
}

@media (min-width: $desktop-version) {
  .address__img-map {
    width: 100%;
    height: 400px;
  }
}
.address__point-img {
  position: absolute;
  top:  177px;
  left: 136px;
}

@media (min-width: $tablet-version) {
  .address__point-img {
    width: 113px;
    height: 106px;

    top: 150px;
    left: 333px;
  }
}


@media (min-width: $desktop-version) {
  .address__point-img {
    width: 113px;
    height: 106px;

    top: 150px;
    left: 1009px;
  }
}



.address__street {
  margin: 0;
  padding-top: 31px;
  padding-bottom: 26px;
  font-size: 14px;
  line-height: 20px;
  font-family: "Arial";
  color: #444444;
}


@media (min-width: $tablet-version) {
  .address__street {
    width: 155px;
    padding-right: 70px;

    text-align: right;
    font-size: 16px;
    line-height: 20px;
  }
}

@media (min-width: $tablet-version) {
  .address__street span {
    display: block;
    margin-top: 15px;
  }
}

@media (min-width: $desktop-version) {
  .address__street {
    padding-right: 90px;
  }
}
